}

@media screen and (max-width: 1000px) {
}

@media screen and (max-width: 768px) {
  .ant-carousel .slick-next {
    inset-inline-end: 15px;
  }

  .ant-carousel .slick-prev {
    inset-inline-start: 5px;
  }
}

